.dpmsg-IconEllipsis,
.dpmsg-IconEllipsis::after,
.dpmsg-IconEllipsis::before {
  background-color: var(--gray-400);
  border-radius: 100%;
  border-radius: 50%;
  display: inline-block;
  height: 3px;
  width: 3px;
}

.dpmsg-IconEllipsis {
  margin: 0 6px;
  position: relative;

  &::after,
  &::before {
    content: "";
    position: absolute;
  }

  &::after {
    right: -5px;
    top: 0;
  }

  &::before {
    left: -5px;
    top: 0;
  }
}

